<template>
    <div>
        <div class="titleBar">
            <el-row justify="space-between" type="flex">
                <el-col></el-col>
                <el-col style="width:56px">
                    <el-button :plain="true" @click="addImmigration" size="small" type="success"></el-button>
                </el-col>
            </el-row>
        </div>

        <el-table :border="true" :data="immigrationList" :stripe="true">
            <el-table-column label="ID" prop="id" width="50px"></el-table-column>
            <el-table-column label="" width="70px" prop="orderNum">
                <template slot-scope="scope">
                    <el-input size="mini" style="text-align: center!important;" v-model="scope.row.orderNum" @change="order(scope.row.orderNum, scope.row.id)"></el-input>
                </template>
            </el-table-column>
            <el-table-column label="" prop="title"></el-table-column>
            <el-table-column label="" prop="status" width="80px">
                <template slot-scope="scope">
                    <el-tag v-if="scope.row.status==0" type="danger"></el-tag>
                    <el-tag v-if="scope.row.status==1" type="success"></el-tag>
                </template>
            </el-table-column>
            <el-table-column label="" prop="createTime" :formatter="dateFormatter" width="200px"></el-table-column>
            <el-table-column label="" width="180px">
                <template slot-scope="scope">
                    <el-button size="small" type="text" @click="recommend(scope.row.id,scope.row.title)" ></el-button>
                    <el-button size="small" @click="edit(scope.row.id)" type="text"></el-button>
                    <el-button size="small" @click="doDelete(scope.$index, scope.row)" type="text"></el-button>
                    <el-button size="small" v-if="scope.row.status==0"  @click="up(scope.row.id)" type="text"></el-button>
                    <el-button size="small" v-if="scope.row.status==1"  @click="down(scope.row.id)" type="text"></el-button>
                </template>
            </el-table-column>
        </el-table>

        <el-dialog
                title=""
                :visible.sync="recommendDialogVisible"
        >
            <el-transfer v-model="recommendPosition" :titles="['', '']" :data="positionList"></el-transfer>
            <span slot="footer" class="dialog-footer">
                <el-button @click="recommendDialogVisible = false"> </el-button>
                <el-button type="primary" @click="doRecommend"> </el-button>
            </span>
        </el-dialog>

        <el-pagination @current-change="handleImmigrationPageChange" :current-page="immigrationCurrentPage" :page-size="immigrationPageSize" @size-change="handleImmigrationPageSizeChange" :total="immigrationTotalCount"></el-pagination>

    </div>
</template>
<script>
    import axios from 'axios';
    import Qs from 'qs/dist/qs.js';
    import moment from 'moment';

    export default {
        name:'null',
        data: function () {
            return {
                immigrationPageSize:20,
                immigrationCurrentPage:0,
                immigrationTotalCount:0,
                immigrationList:[],
                positionList:[],
                recommendDialogVisible:false,
                recommendPosition:[],
                recommendTitle:"",
                recommendId:0,
            }
        },
        methods:{

            order:function (value,id) {
                var self = this;
                axios.post('dayu/immigration/order',Qs.stringify({id:id,orderNum:value}))
                        .then(function (response) {
                            if (response.data.resultCode == 0) {
                                self.SearchImmigrationWithPage();
                            } else {

                            }
                        })
                        .catch(function (response) {

                                }
                        );
            },

            up:function (id) {
                var self = this;
                axios.post('dayu/immigration/up',Qs.stringify({id:id}))
                        .then(function (response) {
                            if (response.data.resultCode == 0) {
                                self.SearchImmigrationWithPage();
                                self.$message("")
                            } else {
                                self.$message("")
                            }
                        })
                        .catch(function (response) {
                                    self.$message("")
                                }
                        );
            },

            down:function (id) {
                var self = this;
                axios.post('dayu/immigration/down',Qs.stringify({id:id}))
                        .then(function (response) {
                            if (response.data.resultCode == 0) {
                                self.SearchImmigrationWithPage();
                                self.$message("")
                            } else {
                                self.$message("")
                            }
                        })
                        .catch(function (response) {
                                    self.$message("")
                                }
                        );
            },

            edit: function (id) {
                this.$router.push('/editImmigration/'+id);
            },

            doDelete:function(index,row){
                var self = this;
                this.$confirm(', ?', '', {
                    confirmButtonText: '',
                    cancelButtonText: '',
                    type: 'warning',
                    center: true
                }).then(() => {
                    axios.post('dayu/immigration/delete',Qs.stringify({id:row.id}))
                            .then(function (response) {
                                if (response.data.resultCode == 0) {
                                    self.SearchImmigrationWithPage();
                                    self.$message('');
                                } else {
                                    self.$message('');
                                }
                            })
                            .catch(function (response) {
                                self.$message('');
                                    }
                            );
                }).catch(() => {
                });
            },

            addImmigration: function () {
                this.$router.push('/addImmigration');
            },

            dateFormatter: function (row, column,cellValue) {
                return moment(cellValue).format("YYYY-MM-DD HH:mm:ss");
            },

            SearchImmigrationWithPage:function(){
                var self = this;
                axios.post('dayu/immigration/pageList',Qs.stringify({pageSize:self.immigrationPageSize,pageNo:self.immigrationCurrentPage}))
                        .then(function (response) {
                            if (response.data.resultCode == 0) {
                                self.immigrationList = response.data.list;
                                self.immigrationTotalCount = response.data.totalRecord;
                            } else {
                                console.log("");
                            }
                        })
                        .catch(function (response) {
                                    console.log(response);
                                }
                        );
            },

            doDeleteImmigration:function(index,row){
                var self = this;
                this.$confirm(', ?', '', {
                    confirmButtonText: '',
                    cancelButtonText: '',
                    type: 'warning',
                    center: true
                }).then(() => {
                    axios.post('dayu/immigration/delete',Qs.stringify({id:row.id}))
                            .then(function (response) {
                                if (response.data.resultCode == 0) {
                                    self.SearchImmigrationWithPage();
                                } else {
                                    alert("");
                                }
                            })
                            .catch(function (response) {
                                        console.log(response);
                                    }
                            );
                }).catch(() => {
                });
            },

            handleImmigrationPageSizeChange:function(size){
                var self = this;
                self.immigrationPageSize = size;
                self.SearchImmigrationWithPage();
            },

            handleImmigrationPageChange:function(currentPage){
                var self = this;
                self.immigrationCurrentPage = currentPage;
                self.SearchImmigrationWithPage();
            },
            recommend:function(id,name){
                this.recommendDialogVisible = true;
                this.selectedPosition(id);
                this.recommendTitle = name;
                this.recommendId = id;
            },

            doRecommend:function () {
                var self = this;
                axios.post('dayu/recommend/recommend',Qs.stringify({positionList:self.recommendPosition,contentId:self.recommendId,title:self.recommendTitle,type:"immigration"}))
                        .then(function (response) {
                            if (response.data.resultCode == 0) {
                                self.$message("");
                                self.recommendDialogVisible = false;
                            } else {
                                self.$message("");
                            }
                        })
                        .catch(function (response) {
                                    self.$message("");
                                }
                        );
            },

            selectedPosition:function (contendId) {
                var self = this;
                self.recommendPosition = [];
                axios.post('dayu/recommend/positionList',Qs.stringify({id:contendId,type:"immigration"}))
                        .then(function (response) {
                            if (response.data.resultCode == 0) {
                                self.recommendPosition = response.data.list;
                            } else {
                                self.$message("");
                            }
                        })
                        .catch(function (response) {
                                    self.$message("");
                                }
                        );
            },
            searchPosition:function(){
                var self = this;
                self.positionList = [];
                axios.post('dayu/recommendPosition/list?type=immigration')
                        .then(function (response) {
                            if (response.data.resultCode == 0) {
                                for (var i=0;i<response.data.list.length;i++){
                                    self.positionList.push({
                                        key:response.data.list[i].id,
                                        label:response.data.list[i].name,
                                    })
                                }
                            } else {
                                self.$message("");
                            }
                        })
                        .catch(function (response) {
                                    self.$message("");
                                }
                        );
            }

        },
        created:function() {
            var self = this;
            self.SearchImmigrationWithPage();
            self.searchPosition();
        }
    }
</script>

<style>
    .titleBar{height: 40px;line-height: 40px;margin-bottom: 20px;}
    .el-pagination{padding: 10px;background-color: #eef1f6;border: 1px solid #e6ebf5;border-top: 0px;}
</style>



