              </el-submenu>
              <el-submenu index="2">
                  <template slot="title"><i class="el-icon-dayu-xiangmu"></i></template>
                  <el-menu-item index="/passport"></el-menu-item>
                  <el-menu-item index="/house"></el-menu-item>
                  <el-menu-item index="/immigration"></el-menu-item>
              </el-submenu>
              <el-submenu index="3">
                  <template slot="title"><i class="el-icon-dayu-zixun"></i></template>
                  <el-menu-item index="/advisory"></el-menu-item>
              </el-submenu>
              <el-submenu index="4">
                  <template slot="title"><i class="el-icon-dayu-slider"></i></template>
                  <el-menu-item index="/sliderPosition"></el-menu-item>
                  <el-menu-item index="/slider"></el-menu-item>
              </el-submenu>
              <el-submenu index="6">
                  <template slot="title"><i class="el-icon-dayu-recommend"></i></template>
                  <el-menu-item index="/recommendPosition"></el-menu-item>
