      <div id="container">
          <el-container style="height: 100%;width: 100%;">
              <el-main>
                <router-view></router-view>
              </el-main>
          </el-container>
      </div>

  </div>
</template>

<script>
export default {
  name: 'App'
}
</script>

<style scoped>

    .el-header{
        padding: 0px;
    }

    .el-menu {
        border-right: 0px;
    }

    #sidebar {
        border-right: 0px solid #e6e6e6;
        background-color: #eef1f6;
        width: 230px;
        position: absolute;
        left: 0px;
        bottom: 0px;
        top: 61px;
        padding: 10px;
    }

    #container {
        padding: 0px;
        position: absolute;
        bottom: 0px;
        top: 61px;
        left: 250px;
        right: 0px;
    }

    body {
        padding: 0px;
        margin: 0px;
    }

    .header {
        height: 60px;
        line-height: 60px;
        background: #20a0ff;
        color: #fff;
    }
</style>



